<template>
  <div>
    <el-row>
      <el-col :span="6">
        <div class="search-item">
          <div class="label">宝宝乳名</div>
          <div class="content">
            <el-input
              v-model="searchForm.babyName"
              placeholder="请输入宝宝乳名"
              size="mini"
            />
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="search-item">
          <div class="label">套餐名称</div>
          <div class="content">
            <service-package v-model="searchForm.serviceId"></service-package>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="search-item">
          <div class="label">用户</div>
          <div class="content">
            <el-input
              v-model="searchForm.userName"
              placeholder="请输入用户昵称或备注"
              size="mini"
            />
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="search-item">
          <div class="label">用户ID</div>
          <div class="content">
            <el-input
              v-model="searchForm.userId"
              placeholder="请输入用户ID"
              size="mini"
            />
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row style="margin-top: 30px;">
      <el-col :span="6">
        <div class="search-item">
          <div class="label">健康管理师</div>
          <div class="content">
            <el-select
              v-model="searchForm.healtherId"
              size="mini"
              placeholder="请选择"
            >
              <el-option
                v-for="item in nurses"
                :key="item._id"
                :label="item.name"
                :value="item._id"
              />
            </el-select>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="search-item">
          <div class="label">宝宝ID</div>
          <div class="content">
            <el-input
              v-model="searchForm.babyId"
              placeholder="请输入宝宝ID"
              size="mini"
            />
          </div>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="search-item search-button">
          <div>
            <el-button type="primary" size="mini" @click="startSearch"
              >搜索</el-button
            >
          </div>
          <div>
            <el-button size="mini" @click="reset">重置</el-button>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapMutations } from 'vuex'
import ServicePackage from '@/components/SearchItem/ServicePackage'

export default {
  name: 'SearchConditions',
  data() {
    return {
      searchForm: {
        babyName: '',
        serviceId: '',
        userName: '',
        userId: '',
        healtherId: '',
        babyId: ''
      },
      nurses: []
    }
  },
  components: {
    ServicePackage
  },
  methods: {
    ...mapActions({
      getBabyList: 'health/getBabyList'
    }),
    ...mapMutations({
      setCurrentPage: 'health/SET_CURRENTPAGE',
      setPageSize: 'health/SET_PAGESIZE'
    }),
    // 搜索前页码组件复位
    startSearch() {
      this.setCurrentPage(1)
      this.setPageSize(10)
      this.search()
    },
    // 搜素
    search() {
      const params = {
        show_replace: 1,
        service_item: 'recipe_service'
      }
      if (this.searchForm.babyName) {
        params.baby_name = this.searchForm.babyName
      }
      if (this.searchForm.userName) {
        params.user_name = this.searchForm.userName
      }
      if (this.searchForm.userId) {
        params.user_id = this.searchForm.userId
      }
      if (this.searchForm.serviceId) {
        params.fd_service_type = this.searchForm.serviceId
      }
      if (this.searchForm.healtherId) {
        params.hm = this.searchForm.healtherId
      }
      if (this.searchForm.babyId) {
        params.baby_id = this.searchForm.babyId
      }
      this.getBabyList(params)
    },
    // 重置
    reset() {
      this.setCurrentPage(1)
      this.setPageSize(10)
      this.searchForm = {
        babyName: '',
        userName: '',
        userId: '',
        serviceId: '',
        healtherId: '',
        babyId: ''
      }
      const params = {
        show_replace: 1,
        service_item: 'recipe_service'
      }
      this.getBabyList(params)
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
.choose-customer {
  .search-container {
    .search-item {
      margin-left: 30px;
      width: 280px;
      .label {
        width: 120px;
        text-align: left;
      }
      .content {
        width: 200px;
      }
    }
  }
}
</style>
