<template>
  <div>
    <div class="step-container">
      <el-steps :active="active">
        <el-step title="选择服务对象" />
        <el-step title="编辑定制食谱任务" />
        <el-step title="完成" />
      </el-steps>
    </div>
    <div v-show="active === 0">
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        :center="true"
        width="22%"
      >
        请选择新建食谱任务的宝宝后再进行下一步操作
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false"
            >确 定</el-button
          >
        </span>
      </el-dialog>
      <div class="choose-customer">
        <div class="search-container">
          <search-conditions ref="searchConditions" />
        </div>
        <div style="text-align: left;margin-bottom: 20px">
          <el-button type="primary" size="mini" @click="gotoStepTwo"
            >下一步</el-button
          >
        </div>
        <baby-list
          ref="babyList"
          :page-type="'recipe_service'"
          @searchBabyList="searchBabyList"
        />
      </div>
    </div>
    <div v-if="active === 1 || active === 2" class="edit-container">
      <el-row>
        <el-col :span="6">
          <baby-info-area :baby-id="selectBaby._id" :page-type="'recipes'" />
        </el-col>
        <el-col :span="18">
          <div class="edit-content">
            <el-row class="edit-section">
              <el-col v-if="active === 1" class="edit-form" :span="24">
                <div class="user-info">
                  <div class="user-info-label">
                    <el-avatar
                      :size="'small'"
                      :src="selectBaby.user_info && selectBaby.user_info.avatar"
                    />
                    <div>&nbsp;&nbsp;用户:</div>
                  </div>
                  <div>
                    {{ selectBaby.user_info && selectBaby.user_info.nickname }}
                    &nbsp;&nbsp;<svg-icon
                      v-if="selectBaby.is_replace"
                      icon-class="replace"
                    />
                  </div>
                </div>
                <div>
                  <el-form ref="ruleForm" :model="ruleForm" label-width="130px">
                    <el-form-item
                      label="模板名称："
                      prop="recipe_template"
                      :rules="[
                        {
                          required: true,
                          message: '请填选择模板',
                          trigger: 'change'
                        }
                      ]"
                    >
                      <el-select
                        v-model="ruleForm.recipe_template"
                        style="width: 75%"
                        @change="getRecipesTitle"
                      >
                        <el-option
                          v-for="item in templateDic"
                          :key="item._id"
                          :label="item.title"
                          :value="item._id"
                        />
                      </el-select>
                    </el-form-item>
                    <el-form-item
                      label="食谱名称："
                      prop="title"
                      :rules="[
                        {
                          required: true,
                          message: '请填写标题',
                          trigger: 'blur'
                        }
                      ]"
                    >
                      <div style="width: 75%">
                        <el-input
                          v-model="ruleForm.title"
                          maxlength="50"
                          show-word-limit
                        />
                      </div>
                    </el-form-item>
                    <el-form-item label="备注：" prop="desc">
                      <el-input
                        v-model="ruleForm.desc"
                        type="textarea"
                        maxlength="100"
                        show-word-limit
                        rows="5"
                      />
                    </el-form-item>
                    <el-form-item
                      label="任务执行日期："
                      prop="executeTime"
                      :rules="[
                        {
                          required: true,
                          message: '请选择任务执行日期',
                          trigger: 'blur'
                        }
                      ]"
                    >
                      <div style="width: 20%">
                        <el-date-picker
                          v-model="ruleForm.executeTime"
                          :picker-options="pickerOptions"
                          type="date"
                          placeholder="请选择任务执行日期"
                          style="width: 100%;"
                        />
                      </div>
                    </el-form-item>
                    <el-form-item>
                      <div class="button-container">
                        <el-button @click="active--">上一步</el-button>
                        <el-button type="primary" @click="createCustomize"
                          >确认创建</el-button
                        >
                      </div>
                    </el-form-item>
                  </el-form>
                </div>
              </el-col>
              <el-col v-if="active === 2">
                <div class="success-container">
                  <div class="success-title">
                    <i class="el-icon-success" />创建定制食谱任务成功
                  </div>
                  <div class="success-item">
                    <div class="label">
                      <div class="avatar">
                        <el-avatar
                          :size="'small'"
                          :src="
                            selectBaby.user_info && selectBaby.user_info.avatar
                          "
                        />
                      </div>
                      用户:
                    </div>
                    <div class="content">
                      {{
                        selectBaby.user_info && selectBaby.user_info.nickname
                      }}
                      &nbsp;&nbsp;<svg-icon
                        v-if="selectBaby.is_replace"
                        icon-class="replace"
                      />
                    </div>
                  </div>
                  <div class="success-item baby-item">
                    <div class="label">宝宝:</div>
                    <div class="content">{{ selectBaby.name }}</div>
                  </div>
                  <div class="success-item">
                    <div class="label">模板名称:</div>
                    <div class="content">{{ templateTitle }}</div>
                  </div>
                  <div class="success-item">
                    <div class="label">食谱名称:</div>
                    <div class="content">{{ ruleForm.title }}</div>
                  </div>
                  <div class="success-item">
                    <div class="label">备注:</div>
                    <div class="content">
                      {{ ruleForm.desc }}
                    </div>
                  </div>
                  <div class="success-item">
                    <div class="label">任务执行日期:</div>
                    <div class="content">
                      {{ ruleForm.executeTime | formatTime('y-m-d') }}
                    </div>
                  </div>
                </div>
                <div v-if="active === 2" class="success-return">
                  <div class="return-text">
                    <div>
                      <span style="color: red">{{ countDown }}S</span
                      >后跳转到定制食谱页面
                    </div>
                    <div
                      style="color:#0085FF; cursor: pointer"
                      @click="returnFollow"
                    >
                      立即返回
                    </div>
                  </div>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import BabyList from '@/components/BabyList'
import BabyInfoArea from '@/views/medical-service/diagnose/extend'
import SearchConditions from './components/SearchConditions'
import { mapGetters, mapMutations, mapActions } from 'vuex'
import { createTask } from '@/api/recipes'

export default {
  name: 'GuidanceEdit',
  components: {
    BabyInfoArea,
    BabyList,
    SearchConditions
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 1000 * 60 * 60 * 24
        }
      },
      dialogVisible: false,
      countDown: 5,
      ruleForm: {
        title: '',
        desc: '',
        executeTime: '',
        recipe_template: ''
      },
      active: 0
    }
  },
  computed: {
    ...mapGetters(['selectBaby', 'templateDic']),
    templateTitle() {
      let title = ''
      this.templateDic.forEach(item => {
        if (item._id === this.ruleForm.recipe_template) {
          title = item.title
        }
      })
      return title
    }
  },
  watch: {
    countDown(val) {
      if (val === 0) {
        this.$router.back(-1)
      }
    },
    selectBaby(val) {
      if (val && val.is_replace) {
        // 如果这个宝宝是代班的，还要请求代班人的食谱列表
        this.getTemplateDic({ show_replace: 1 })
      }
    }
  },
  mounted() {
    // 页面加载清空store的选中宝宝
    this.setSelectBaby(null)
    // 获取食谱模板下拉列表
    this.getTemplateDic()
  },
  methods: {
    ...mapActions({
      getTemplateDic: 'recipes/getTemplateDic'
    }),
    ...mapMutations({
      setSelectBaby: 'health/SET_SELECTBABY'
    }),
    // 分页时查询宝宝列表
    searchBabyList() {
      this.$refs.searchConditions.search()
    },
    getRecipesTitle(val) {
      this.templateDic.forEach(item => {
        if (item._id === val) {
          this.ruleForm.title = item.title
        }
      })
    },
    // 新建自定义任务
    createCustomize() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          const params = { ...this.ruleForm }
          createTask(this.selectBaby._id, params).then(res => {
            if (res.code === 20000) {
              this.active++
              this.startCountDown()
            } else {
              this.$message.warning('任务创建失败')
            }
          })
        } else {
          return false
        }
      })
    },
    gotoStepTwo() {
      if (!this.selectBaby) {
        this.dialogVisible = true
        return
      }
      this.active++
    },
    returnFollow() {
      this.$router.back(-1)
    },
    startCountDown() {
      setInterval(() => {
        this.countDown--
      }, 1000)
    }
  }
}
</script>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
.choose-customer {
  .search-container {
    height: 120px;
  }
}
.success-item {
  .label {
    font-weight: bold;
  }
}
.baby-item {
  position: absolute;
  top: 65px;
  right: 300px;
  width: 500px;
}
</style>
